import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { images, language } from '../../config/constants';
import "./Header.css";
import { Row, Col } from 'react-flexbox-grid';

class Header extends PureComponent {
    render() {
        const { lang } = this.props;
        return (
            <Row className={'wrap-profile'} center="xs">
                <Col xs className={'language'}>
                    {
                        lang == 'es' ? 
                        <img onClick={this.props.changeLang} src="data:image/png;base64," title="English" alt="English" /> :
                        <img onClick={this.props.changeLang} src="data:image/png;base64," title="Espaol" alt="Espaol" />
                    }
                </Col>
                <Col xs={12}>                    
                    <img
                        className={'img-profile'}
                        src={images.profile}
                        alt={'Hayder Espinoza'}
                        width={200} height={200}
                    />
                    <h1 className={'name'}>Hayder Espinoza B</h1>
                    <h2 className={'work'}>{ language[lang].work }</h2>
                </Col>
            </Row>
        );
    }
}

Header.propTypes = {

};

export default Header;


